import React, { Fragment } from 'react';
import Logo from '../../assets/logo.png';
import './_navbar.scss';

/**
 * Navbar of app
 * @component top navbar with links for Homepage, Profile page, Rglage, Comunity.
 */

export default function Navbar() {
  return (
    <Fragment>
      <nav className="navbar">
        <img src={Logo} alt="logo of sportsee" />
        <ul className="navbar__list">
          <li>Accueil</li>
          <li>Profil</li>
          <li>Rglage</li>
          <li>Communaut</li>
        </ul>
      </nav>
    </Fragment>
  );
}
